<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div v-if="showdata">
          <div class="detail_price"></div>
          <div id="myChart" :style="{width: '100%', height: '5rem'}"></div>
          <div class="tradedatacontra">
            <yd-flexbox class="tradedatacontra_tit">
                <yd-flexbox-item>{{$t('data.into')}}</yd-flexbox-item>
                <yd-flexbox-item>{{$t('data.inout')}}</yd-flexbox-item>
                <yd-flexbox-item>{{$t('data.combined')}}</yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox>
                <yd-flexbox-item>{{TotalInAmount}}</yd-flexbox-item>
                <yd-flexbox-item>{{TotalOutAmount}}</yd-flexbox-item>
                <yd-flexbox-item>{{TotalSumAmount}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
          <!-- 交易记录- -->
          <div class="det_list" style="padding-bottom: 1.2rem">
            <div class="det_list_title">
                <span>{{$t('code.trade')}}</span>
                <span @click="tradeShow=true">{{$t('data.screening')}}</span>
                <div class="clear"></div>
            </div>
              <div class="pro_list" style="margin-top:0">
                <div v-if="list.length>0">
                    <yd-flexbox class="pro_list_item" v-for="a in list" @click.native="goorder(a)">
                        <yd-flexbox-item  class="pro_list_price">
                            <span style="margin-bottom: .1rem">{{a.Symbol}}</span>
                            <span>{{a.TradeTime}}</span>
                        </yd-flexbox-item>
                        <div  class="pro_list_ps red" :class="{'red':a.tradeType==0}" v-if="a.tradeType==0">
                            -{{a.Value}} {{a.Symbol}}
                        </div>
                        <div  class="pro_list_ps red" :class="{'green':a.tradeType==1}" v-else>
                            +{{a.Value}} {{a.Symbol}}
                        </div>
                    </yd-flexbox>
                </div>
                <div class="notradedata" v-else>
                    <p>{{$t('toast.nolist')}}</p>
                </div>
            </div>
          </div>
        </div>
        <div class="zw_ord" v-else>
          <img src="../assets/images/ord.png">
          <p>{{$t('toast.nolist')}}</p>
        </div>

        <!-- //按钮- -->
        <div class="code_btn">
          <div class="left_btn"  @click="gozhuanzhang('555')">
            <yd-flexbox>
                <yd-flexbox-item style="text-align: right;"><img src="../assets/images/zhan.png"></yd-flexbox-item>
                <yd-flexbox-item style="text-align: left;">{{$t('code.zhuanzhang')}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
          <div class="right_btn" @click="goqcode()">
            <yd-flexbox>
                <yd-flexbox-item style="text-align: right;"><img src="../assets/images/skk.png"></yd-flexbox-item>
                <yd-flexbox-item style="text-align: left;">{{$t('code.shoukuan')}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
        </div>

        <!-- 筛选交易记录 -->
        <yd-popup v-model="tradeShow" position="bottom" height="35%">
            <div class="chooseBtn">
                <button @click="tradeShow=false">{{$t('code.cancel')}}</button>
                <button @click="tradeChange(searchKey,datetime),tradeShow=false">{{$t('data.query')}}</button>
                <div class="clear"></div>
            </div>
            <div class="choosetool">
                <p>{{$t('data.type')}}</p>
                <yd-flexbox class="choosetool_type">
                    <yd-flexbox-item><span :class="{'choosetool_type_active':searchKey=='In'}" @click="searchKey='In'">{{$t('data.into')}}</span></yd-flexbox-item>
                    <yd-flexbox-item><span :class="{'choosetool_type_active':searchKey=='Out'}" @click="searchKey='Out'">{{$t('data.inout')}}</span></yd-flexbox-item>
                    <yd-flexbox-item><span :class="{'choosetool_type_active':searchKey=='total'}" @click="searchKey='total'">{{$t('data.combined')}}</span></yd-flexbox-item>
                </yd-flexbox>
            </div>
            <div class="chooseTime">
                <p>{{$t('tuiguang.riqi')}}</p>
                <yd-datetime :start-date="startdate" type="date" :end-date="enddate" v-model="datetime"></yd-datetime>
            </div>
            
        </yd-popup>
    </div>
  
</template>

<script>
import headerBar from "./header.vue";
import { getCookie, setCookie } from "../assets/js/cookie.js";
import {formatDateTime} from '@/utils/utils'
export default {
  components: {
    headerBar: headerBar
  },
  beforeCreate() {
    setTimeout(function() {
      document
        .querySelector("body")
        .setAttribute("style", "background:#ffffff");
    }, 10);
  },
  beforeDestroy() {
    document.querySelector("body").setAttribute("style", "background:#ffffff");
  },
  data() {
    return {
      headerNavData: {
        left: true,
        title: this.$t('data.detail'),
        right: false,
        bj: "#2c94ed",
        search: false,
        share: false
      },
      searchKey:'total',
      datetime:formatDateTime(Math.round(new Date().getTime()/1000)),
      enddate:formatDateTime(Math.round(new Date().getTime()/1000)),
      startdate:formatDateTime(Math.round(new Date().getTime()/1000)-30*24*60*60),
      tradeShow:false,
      showdata: false,
      page: 1,
      pagesize: 10,
      pagenum: 0,
      list: [],
      lasttime: "",
      cx: [],
      cy_InAmount: [],
      cy_OutAmount: [],
      cy_SumAmount: [],
      TotalInAmount: "",
      TotalOutAmount: "",
      TotalSumAmount: "",
      Details:''
    };
  },
  created() {
    this.headerNavData.title = this.$route.query.name;
    this.tradelist();
  },
  mounted(){
  },
  updated(){
    this.drawLine();
  },
  methods: {
    tradelist() {
      this.openloading(this.$t('toast.loding'));
      var that = this;
      this.axios({
        url: this.Url + "EthTrade.ashx",
        method: "post",
        params: {
            cmd: "statistics",
            contract: this.$route.query.contractAddress,
            publickey: getCookie("publickey")
        }
      }).then(function(res) {
          that.closeloading();
          var _list = res.data.data.Details;
          if (res.data.success) {
            that.showdata = true;
            that.TotalInAmount = res.data.data.TotalInAmount;
            that.TotalOutAmount = res.data.data.TotalOutAmount;
            that.TotalSumAmount = res.data.data.TotalSumAmount;
            that.Details = _list
            _list.forEach(function(item) {
                var t = item.Date.split("T")[0].substring(0, 10);
                that.cx.push(t);
                that.cy_InAmount.push(item.InAmount);
                that.cy_OutAmount.push(item.OutAmount);
                that.cy_SumAmount.push(item.SumAmount);
            });
            that.tradeChange(that.searchKey,that.datetime)
          } else {
            that.showdata = false;
            return;
          }
        }).catch(function(res) {
          that.closeloading();
          res = JSON.stringify(res);
          if (JSON.stringify(JSON.parse(res).request) == "{}") {
            that.catchErr();
            setTimeout(function() {
              that.$dialog.confirm({
                title: that.$t('data.networkerr')+'...',
                opts: res => {
                  that.tradelist();
                }
              });
            }, 2000);
          }
        });
    },
    tradeChange(obj,t){
        var data = this.Details;
        this.list=[]
        for(var i=0;i<data.length;i++){
            var list_t=data[i].Date.split("T")[0].substring(0, 10)
            if(t==list_t){
                if(obj == "In"){
                    this.list=data[i].In
                }else if(obj == "Out"){
                    this.list=data[i].Out
                }else{
                    this.list=[...data[i].In,...data[i].Out]
                }
            }else{
                this.list=[]
            }
        }
    },
    //跳转交易记录详情
    goorder(item) {
      var data = JSON.stringify(item);
      this.$router.push({
        path: "/index_order_detail",
        query: { data: data, type: 1 }
      });
    },
    //跳转收款码
    goqcode() {
      this.$router.push({
        path: "/qcode",
        query: { num: "565sdf55652dfc55gh23" }
      });
    },
    //跳转转账
    gozhuanzhang(id) {
      this.$router.push({
        path: "/zhuanzhang",
        query: {
          name: this.$route.query.name,
          contractAddress: this.$route.query.contractAddress
        }
      });
    },
    drawLine() {
      var that = this;
      var InAmount = that.cy_InAmount;
      var OutAmount = that.cy_OutAmount;
      var SumAmount = that.cy_SumAmount;
      var xAxisnum = that.cx;
      // 基于准备好的dom，初始化echarts实例
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      // 绘制图表
      var colors = ["#5793f3", "#d14a61", "#675bba"];
      myChart.setOption({
        //backgroundColor: 'rgb(245, 245, 245)',
        title: {
          //text: '资产数量'
        },
        tooltip: {
          trigger: "axis",
          confine:true,
          axisPointer: {
            //type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          show: true,
          data: ["转入", "转出", "合计"]
        },
        grid: [
          {
            top: 10 + "%",
            bottom: 5 + "%",
            left: 4 + "%",
            right: 3 + "%",
            //height: 95 + '%',
            containLabel: true
          }
        ],
        xAxis: {
          type: "category",
          axisTick: {
            alignWithLabel: true
          },
          scale: true,
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "#c3c3c3"
            }
          },
          boundaryGap: true,
          splitLine: { show: false },
          splitNumber: 20,
          axisLabel: {
            textStyle: {
              color: "#666" //坐标值得具体的颜色
            }
          },
          data: xAxisnum
        },
        yAxis: {
          type: "value",
          position: "right",
          axisLine: {
            onZero: false,
            lineStyle: {
              color: "#c3c3c3"
            }
          },
          axisTick: {
            show: false
          },
          splitLine: {
            show: false
          }
        },
        dataZoom: [
          {
            type: "inside",
            realtime: true
          }
        ],
        series: [
          {
            name: "转入",
            type: "line",
            smooth: true,
            stack: "转入",
            showSymbol: false,
            lineStyle: {
              normal: {
                width: 1
                //color: 'rgb(39, 142, 237)',
              }
            },
            data: InAmount
          },
          {
            name: "转出",
            type: "line",
            stack: "转出",
            smooth: true,
            showSymbol: false,
            lineStyle: {
              normal: {
                width: 1
                //color: '#ff5722',
              }
            },
            data: OutAmount
          },
          {
            name: "合计",
            type: "line",
            stack: "合计",
            smooth: true,
            showSymbol: false,
            lineStyle: {
              normal: {
                width: 1
                //color: '#009688',
              }
            },
            data: SumAmount
          }
        ]
      });
    }
  }
};
function eConsole(params) {
  console.log(params);
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
@import "../assets/css/index_detail.css";
</style>




